import React from 'react';
import { Container } from './Slide.styles';

const Slide = props => {
  console.log('slide rendered');
  return <Container {...props}>{props.children}</Container>;
};

const isStillHidden = (prevProps, nextProps) => prevProps.visible === nextProps.visible && prevProps.scale === nextProps.scale;

export default React.memo(Slide, isStillHidden);
